.suggested-button {
    border: '5px solid';
    background: linear-gradient(to right, #f17f7f 0%, #60c6a4 19%, #29b6d9 42%, #7742d7 79%, #27bfe5 100%);
    border-image-slice: 1;
    border-radius: 5px;
    z-index: 1;
}

.suggested-button::before {
    content: "";
    position: absolute;
    top: 1px;
    right: 1px;
    bottom: 1px;
    left: 1px;
    border-radius: 4px;
    background: white;
    z-index: -1;
}

